html , body{
  min-height: 100% ;
}


ul,
ol {
	list-style: none;
  margin: 0;
  padding: 0;
}


body {
  font-family: 'Rubik', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;

  margin: 0;
	padding: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;


  background: #2c5364; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#2c5364,
		#203a43
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#2c5364,
		#203a43
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100% ;
}


header {
	width: fit-content;
}

.icon-container svg {
  transition: all .3s ease-in ;
}

.icon-container:hover svg,
.icon-container:focus svg,
.icon-container.active svg{
  background-color: #2c5364;
  color: #fff ;
}
